.slider.light-arrows.rsHor .rsArrow .rsArrowIcn {
	&:before,
	&:after {
		background-color: $white;
	}
}
.slider.dark-arrows.rsHor .rsArrow .rsArrowIcn {
	&:before,
	&:after {
		background-color: $grey;
	}
}
.slider {
	position: relative;

	.rsGCaption {
		left: 0;
		bottom: 0;
		width: 100%;
		text-align: center;
		-webkit-box-shadow: inset 0px -100px 10px 0px rgba(0,0,0,0.3);
		-moz-box-shadow: inset 0px -100px 10px 0px rgba(0,0,0,0.3);
		box-shadow: inset 0px -100px 10px 0px rgba(0,0,0,0.3);
		background-color: transparent;
		z-index: 100;
		.link {
			color: $white;
			display: inline-block;
			padding: 5px 30px;
			border-top: 1px solid $white;
			border-bottom: 1px solid $white;
		}
	}
	@include respond-to('xs') {
		height: 300px;
	}

	@include respond-to('sm-p') {
		height: 500px;
	}

	&,
	& .rsOverflow,
	& .rsSlide,
	& .rsVideoFrameHolder,
	& .rsThumbs {
		background-color: $white;
	}

	&.rsHor .rsArrow .rsArrowIcn {
		width: 70px;
		height: 70px;
		margin-top: -35px;
		background: none;
		&:before,
		&:after {
			content: '';
			width: 2px;
			height: 40px;
			display: block;
			position: absolute;
			top: 50%;
		}
	}
	&.rsHor .rsArrowLeft .rsArrowIcn {
		margin-left: -22px;
		&:before {
			transform: rotate(-45deg) translate(39px, 28px);
		}
		&:after {
			transform: rotate(45deg) translate(8px, -58px);
		}
	}
	&.rsHor .rsArrowRight .rsArrowIcn {
		margin-left: -48px;
		&:before {
			transform: rotate(-45deg) translate(38px, -11px);
		}
		&:after {
			transform: rotate(45deg) translate(9px, -18px);
		}
	}
}

.visible-nearby-slider {

	.slide {
		color: inherit;
		opacity: .3;
		display: block;
		padding: 20px;
		background-color: $lighter-grey-l-10;
		margin-right: 10px;
		transition: opacity 1s;
		&.is-selected {
			opacity: 1;
		}
	}

	.flickity-prev-next-button {
		background-color: transparent;
		width: 88px;
		height: 88px;
	}

	.flickity-prev-next-button .arrow {
		fill: $grey;
	}

	.bg-image { background-size: cover; }
	.text-container {
		text-align: center;
		margin-top: 10px;
		overflow: hidden;
		.title {
			margin-bottom: 10px;
		}
	}

	@include respond-to('xs') {
		min-height: 350px;
		.slide { width: 100%; }
		.bg-image { height: 250px; }
	}
	@include respond-to('sm') {
		min-height: 400px;
		.slide { width: 80%; }
		.bg-image { height: 300px; }
	}
	@include respond-to('md-p') {
		min-height: 450px;
		.slide { width: 60%; }
		.bg-image { height: 350px; }
	}
}